.table {
  display: table;
}

.table-row {
  display: table-row;
}

.table-cell {
  display: table-cell;
  padding: 2px 6px;
  vertical-align: middle;
}

.table-outline {
  border: 1px solid #d1d4d7;
}
.table-outline td {
  vertical-align: middle;
}

.table-align-middle td {
  vertical-align: middle;
}

.table-clear td {
  border: 0;
}

.table thead th, .table thead td {
  background: #d1d4d7;
  color: #800080;
}

thead th a, thead th a:not([href]):not([tabindex]) {
  color: #800080 !important;
}

thead tr:first-child {
  border-top: none;
}
thead tr:first-child th, thead tr:first-child td {
  border-top: none;
}

thead tr:first-child th:first-child {
  border-top-left-radius: 4px;
}

thead tr:first-child th:last-child {
  border-top-right-radius: 4px;
}

tbody tr:last-child td:first-child {
  border-bottom-left-radius: 4px;
}

tbody tr:last-child td:last-child {
  border-bottom-right-radius: 4px;
}

.table-striped tbody tr:nth-of-type(odd) td {
  background-color: #F5F0F5;
}
.table-striped tbody tr:nth-of-type(even) td {
  background-color: #F8FFFF;
}
.table-striped tbody tr:nth-of-type(odd) {
  background-color: transparent !important;
}
.table-striped tbody tr:nth-of-type(odd) td {
  background-color: #f5f5f7;
}

table.transparent {
  background: transparent !important;
  width: 100% !important;
}
table.transparent tr, table.transparent td, table.transparent th {
  background: transparent !important;
  border: none !important;
}

tr.selected, tr.selected-right {
  border: none;
}
tr.selected td, tr.selected-right td {
  background: #F5F0F5 !important;
  color: #800080 !important;
}

tr.even > td {
  background: #F8FFFF;
}

tr.odd > td {
  background: #F5F0F5;
}

th {
  vertical-align: middle;
}

.btn-primary {
  background: #800080;
  border-color: #800080;
}

.btn-primary:hover {
  background: #B000B0;
  border-color: #B000B0;
}

.gray {
  color: #888;
}

h4, .card-content h4 {
  font-weight: 500 !important;
  font-size: 1.2rem !important;
  color: #444;
  margin-bottom: 1rem;
}

.italic {
  font-style: italic;
}

.card {
  border-radius: 8px;
  font-size: 0.8rem;
}
.card p {
  font-size: 0.8rem;
}
.card h2 {
  font-size: 0.9rem;
  font-weight: 800;
}
.card .card-title {
  font-size: 1.1rem;
  font-weight: 600;
  text-align: center;
  padding: 0;
  margin: auto auto;
}

.underline {
  text-decoration: underline;
  cursor: pointer;
}

.tooltip {
  font-family: Arial, sans-serif;
  font-weight: 800;
}

.tooltip-inner {
  color: #FFF;
  background-color: #38585f;
  font-weight: 300;
}